import { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
    Box,
    Avatar,
    Link,
    Grid,
    Button,
    Typography,
    LinearProgress,
} from "@mui/material";
import {
    InputLabel,
    OutlinedInput,
    FormControl,
} from "@material-ui/core";

import "./ido.scss";

import yes from "../../assets/images/yes.png";
import no from "../../assets/images/no.png";
import IDOLogo from "../../assets/images/IDOLogo2.png";
import maticLogo from "../../assets/images/maticLogo.png";


import metamaskgif from "../../assets/images/metamaskunscreen.gif";

import CountUp from "react-countup";
import "react-awesome-slider/dist/custom-animations/cube-animation.css";
import { useTranslation } from "react-i18next";
import { useMediaQuery } from "react-responsive";
import { calTime, get_Polygon_Main_PriorityFee } from '../../utils';
import IdoTopBar from '../../components/IdoTopBar';
import toast, { Toaster } from "react-hot-toast";
import NP from "number-precision";
import { useWeb3Context } from "../../hooks/web3Context";
import { loadAppDetails } from "../../slices/AppSlice";
import { loadAccountDetails } from "../../slices/AccountSlice";
import { isPendingTxn, txnButtonText } from "../../slices/PendingTxnsSlice";
import useIdo from "../../hooks/useIdo";


function makeToast(_msg = "", _icon = "", _duration = 6000) {
    return toast(
        t => (
            <span
                className="toast"
                role="button"
                tabIndex={0}
                onClick={() => toast.dismiss(t.id)}
                onKeyDown={() => toast.dismiss(t.id)}
            >
                {_msg}
                <span style={{ float: "right", marginLeft: "10px" }}>✖</span>
            </span>
        ),
        {
            icon: _icon,
            duration: _duration,
        },
    );
}



export default function Index() {
    const dispatch = useDispatch();
    const { provider, address, connected, chainID } = useWeb3Context();
    const pendingTransactions = useSelector(state => {
        return state.pendingTransactions;
    });
    const { IDoClaim, Sermon, userApproveACreed_to_Dice, WithdrawalIncome } = useIdo();
    const { t } = useTranslation();
    const isSmallPhone = useMediaQuery({ maxWidth: 800 });

    const minAmount = useSelector(state => { return state.app.appDetails?.minAmount || '0'; });
    const maxAmount = useSelector(state => { return state.app.appDetails?.maxAmount || '0'; });
    const salePrice = useSelector(state => { return state.app.appDetails?.salePrice || '0'; });
    const startTimestamp = useSelector(state => { return state.app.appDetails?.startTimestamp || '0'; });
    const endTimestamp = useSelector(state => { return state.app.appDetails?.endTimestamp || '0'; });
    const FullReleaseTimestamp = useSelector(state => { return state.app.appDetails?.FullReleaseTimestamp || '0'; });
    const toTalAmount = useSelector(state => { return state.app.appDetails?.toTalAmount || '0'; });
    const sellAmount = useSelector(state => { return state.app.appDetails?.sellAmount || '0'; });
    const sharesDiceAll = useSelector(state => { return state.app.appDetails?.sharesDiceAll || '0'; });
    const FullRelease = useSelector(state => { return state.app.appDetails?.FullRelease || '0'; });
    const ProfitRelease = useSelector(state => { return state.app.appDetails?.ProfitRelease || '0'; });
    const SatanDevil = useSelector(state => { return state.app.appDetails?.SatanDevil || 0; });
    const Exodus = useSelector(state => { return state.app.appDetails?.Exodus || '0'; });
    const incomeProfit = useSelector(state => { return state.app.appDetails?.incomeProfit || '0'; });
    const outcomeProfit = useSelector(state => { return state.app.appDetails?.outcomeProfit || '0'; });
    const IdoMaticBalance = useSelector(state => { return state.app.appDetails?.IdoMaticBalance || '0'; });
    const isTimeEnd = useSelector(state => { return state.app.appDetails?.isTimeEnd || false; });
    const Leap_Of_Faith = useSelector(state => { return state.app.appDetails?.Leap_Of_Faith || 1000; });


    const userMaticBalance = useSelector(state => { return state.account?.accountDetails.userMaticBalance || '0'; });
    const userACreedBalance = useSelector(state => { return state.account?.accountDetails.userACreedBalance || '0'; });
    const boughtACreed = useSelector(state => { return state.account?.accountDetails.boughtACreed || false; });
    const whiteListed = useSelector(state => { return state.account?.accountDetails.whiteListed || false; });
    const userSharesDice = useSelector(state => { return state.account?.accountDetails.userSharesDice || '0'; });
    const approveACreed_to_Dice = useSelector(state => { return state.account?.accountDetails.approveACreed_to_Dice || '0'; });


    const [coundown, setCoundown] = useState({ day: '00', hour: '00', minutes: '00', seconds: '00' })
    const [startText, setStartText] = useState('STARTS')
    const [coundownRelease, setCoundownRelease] = useState({ day: '00', hour: '00', minutes: '00', seconds: '00' })
    const [startTextRelease, setStartTextRelease] = useState('STARTS')
    const [companyTotalAssetsFull, setCompanyTotalAssetsFull] = useState('0')
    const [companyTotalAssets, setCompanyTotalAssets] = useState('0')
    const [diceSharesForShow, setDiceSharesForShow] = useState('0')
    const [maticAmount, setMaticAmount] = useState('')
    const [willGetACreed, setWillGetACreed] = useState('0')

    const [burnSharesAmount, setBurnSharesAmount] = useState('')
    const [willGetMatic, setWillGetMatic] = useState('0')
    const [willGetMaticFull, setWillGetMaticFull] = useState('0')
    const [aCreedApproveEnough, setACreedApproveEnough] = useState(false)

    const [polygon_main_priorityFee, setPolygon_main_priorityFee] = useState({
        SafeGasPrice: "60",
        ProposeGasPrice: "120",
        FastGasPrice: "180",
        suggestBaseFee: "0.00000002",
    });
    const { SafeGasPrice, ProposeGasPrice, FastGasPrice, suggestBaseFee } = polygon_main_priorityFee;

    const returnMetaMaskLink = () => {
        if (!isSmallPhone) {
            return 'https://chrome.google.com/webstore/detail/metamask/nkbihfbeogaeaoehlefnkodbefgpgknn';
        }
        const terminal = navigator.userAgent.toLowerCase();
        if ((terminal.indexOf('iphone') != -1) || (terminal.indexOf('ipad') != -1)) {
            return 'https://apps.apple.com/us/developer/metamask/id1438144201';
        }
        else {
            return 'https://play.google.com/store/apps/details?id=io.metamask';
        }

    }

    useEffect(() => {
        dispatch(loadAppDetails({ networkID: chainID, provider: provider }));
        address && dispatch(loadAccountDetails({ networkID: chainID, provider: provider, address: address }));

        const id = setInterval(async () => {
            dispatch(loadAppDetails({ networkID: chainID, provider: provider }));
            address && dispatch(loadAccountDetails({ networkID: chainID, provider: provider, address: address }));
            setPolygon_main_priorityFee(await get_Polygon_Main_PriorityFee());
        }, 50000);

        return () => {
            clearInterval(id);
        };
    }, [chainID, provider, address]);

    useEffect(() => {
        if (Number(burnSharesAmount) > 0 && Number(sharesDiceAll) > 0) {
            if (Number(FullRelease) > 0) {
                const _willGetMatic_ = NP.times(NP.divide(Number(burnSharesAmount), Number(sharesDiceAll)),
                    Number(FullRelease)).toFixed(2)
                setWillGetMatic(_willGetMatic_)
            } else {
                const _willGetMatic_ = NP.times(NP.divide(Number(burnSharesAmount), Number(sharesDiceAll)),
                    Number(ProfitRelease)).toFixed(2)
                setWillGetMatic(_willGetMatic_)
            }

            const _willGetMaticFull_ = NP.times(NP.divide(Number(burnSharesAmount), Number(sharesDiceAll)),
                Number(Exodus)).toFixed(2)
            setWillGetMaticFull(_willGetMaticFull_)
        }
        else {
            setWillGetMatic('0')
            setWillGetMaticFull('0')
        }
    }, [burnSharesAmount, sharesDiceAll, FullRelease, ProfitRelease])

    useEffect(() => {
        Number(FullRelease) > 0 ?
            setCompanyTotalAssets(NP.plus(Number(FullRelease), Number(IdoMaticBalance)).toFixed(2))
            : setCompanyTotalAssets(NP.plus(Number(ProfitRelease), Number(IdoMaticBalance)).toFixed(2))


        setCompanyTotalAssetsFull(NP.plus(Number(Exodus), Number(IdoMaticBalance)).toFixed(2))

    }, [FullRelease, ProfitRelease, Exodus, IdoMaticBalance])

    useEffect(() => {
        Number(approveACreed_to_Dice) > Number(burnSharesAmount) ? setACreedApproveEnough(true) : null
    }, [approveACreed_to_Dice, burnSharesAmount])


    useEffect(() => {
        if (Number(userSharesDice) > 0 && Number(sharesDiceAll) > 0) {
            const percentShares = NP.divide(Number(userSharesDice) * 100, Number(sharesDiceAll)).toFixed(2)
            setDiceSharesForShow(percentShares)
            return;
        }
        setDiceSharesForShow('0')
    }, [userSharesDice, sharesDiceAll])

    useEffect(() => {
        const id = setInterval(async () => {
            let now = new Date().getTime()

            if (endTimestamp && startTimestamp) {
                const startTime = Number(startTimestamp) - now / 1000
                if (startTime > 0) {
                    setStartText("STARTS")
                    setCoundown(calTime(startTime))
                } else {
                    const endTime = Number(endTimestamp) - now / 1000

                    if (endTime > 0) {
                        setStartText("ENDS")
                        setCoundown(calTime(endTime))
                    } else {
                        setStartText("ENDS")
                    }
                }
            }

            if (FullReleaseTimestamp) {
                const releaseTime = Number(FullReleaseTimestamp) - now / 1000
                if (releaseTime <= 0) {
                    setStartTextRelease("HAD STARTED")
                    setCoundownRelease({ day: '00', hour: '00', minutes: '00', seconds: '00' })
                } else {
                    setStartTextRelease("STARTS")
                    setCoundownRelease(calTime(releaseTime))
                }
            }

        }, 1000)
        return () => { clearInterval(id); };
    }, [endTimestamp, startTimestamp, FullReleaseTimestamp])


    const onChangeHandle_IDO = e => {
        e.target.value === "" ? setMaticAmount("") : setMaticAmount(e.target.value);
    }

    const handleInputMax_IDO = () => {
        Number(userMaticBalance) < Number(maxAmount) ? setMaticAmount(userMaticBalance) : setMaticAmount(maxAmount)
    }

    useEffect(() => {
        if (Number(maticAmount) > 0 && Number(Leap_Of_Faith) > 0 && Number(salePrice) > 0) {

            if (isTimeEnd) {
                const sermonAmount = NP.divide(Number(maticAmount), Number(Leap_Of_Faith)).toFixed(4)
                setWillGetACreed(NP.divide(Number(sermonAmount), Number(salePrice)).toFixed(4))
                return;
            }

            const _willGetACreed_ = NP.divide(Number(maticAmount), Number(salePrice)).toFixed(2)
            if (whiteListed === true && Number(_willGetACreed_) > 0) {
                const willGetACreed_whiteListed = NP.divide(
                    NP.times(Number(_willGetACreed_), 103),
                    100
                ).toFixed(2)
                setWillGetACreed(willGetACreed_whiteListed)
                return;
            }
            setWillGetACreed(_willGetACreed_)

        }

    }, [maticAmount, Leap_Of_Faith, salePrice, whiteListed])

    const IDOHandle = async () => {
        if (address === "") {
            makeToast(t('notConnectted'), "⚠️", 6000);
            return;
        }
        if (chainID != 137) {
            makeToast(t('switch'), "⚠️", 6000);
            return;
        }
        if (boughtACreed === true) {
            makeToast(t('alreadyBought'), "⚠️", 6000);
            return;
        }
        if (Number(maticAmount) < Number(minAmount)) {
            makeToast(t("lessMinMatic"), "⚠️", 6000);
            return;
        }
        if (!isTimeEnd && Number(maticAmount) > Number(maxAmount)) {
            makeToast(t("exceedMax"), "⚠️", 6000);
            return;
        }
        if (isTimeEnd && Number(maticAmount) > Number(maxAmount) * Number(Leap_Of_Faith)) {
            makeToast(t("exceedMax"), "⚠️", 6000);
            return;
        }
        if (Number(userMaticBalance) < Number(maticAmount)) {
            makeToast(t('balanceInsufficient'), "⚠️", 6000);
            return;
        }

        setPolygon_main_priorityFee(await get_Polygon_Main_PriorityFee());

        if (provider && chainID && address) {
            !isTimeEnd ?
                await IDoClaim(maticAmount, makeToast,
                    Number(ProposeGasPrice).toFixed(0),
                    NP.plus( Number(ProposeGasPrice), Number(suggestBaseFee) ).toFixed(0),
                )
                :
                await Sermon(maticAmount, makeToast,
                    Number(ProposeGasPrice).toFixed(0),
                    NP.plus( Number(ProposeGasPrice), Number(suggestBaseFee) ).toFixed(0),
                )
        }
    }

    const onChangeHandle_burnShares = e => {
        e.target.value === "" ? setBurnSharesAmount("") : setBurnSharesAmount(e.target.value);
    }
    const handleInputMax_burnShares = () => {
        setBurnSharesAmount(userSharesDice)
    }

    const DiceWithdrawalIncomeHandle = async () => {
        if (address === "") {
            makeToast(t('notConnectted'), "⚠️", 6000);
            return;
        }
        if (chainID != 137) {
            makeToast(t('switch'), "⚠️", 6000);
            return;
        }
        if (Number(burnSharesAmount) <= Number(0)) {
            makeToast(t("amount_ACreed_destroy"), "⚠️", 6000);
            return;
        }
        if (Number(userACreedBalance) < Number(burnSharesAmount)) {
            makeToast(t("ACreedInsufficient"), "⚠️", 6000);
            return;
        }
        if (Number(approveACreed_to_Dice) < Number(burnSharesAmount)) {
            makeToast("notApproved", "⚠️", 6000);
            return;
        }

        setPolygon_main_priorityFee(await get_Polygon_Main_PriorityFee());

        if (provider && chainID && address) {
            await WithdrawalIncome(
                burnSharesAmount, makeToast,
                Number(ProposeGasPrice).toFixed(0),
                NP.plus( Number(ProposeGasPrice), Number(suggestBaseFee) ).toFixed(0),
            )
        }
    }

    const userApproveACreed_to_DiceHandle = async () => {
        if (address === "") {
            makeToast(t('notConnectted'), "⚠️", 6000);
            return;
        }
        if (chainID != 137) {
            makeToast(t('switch'), "⚠️", 6000);
            return;
        }

        setPolygon_main_priorityFee(await get_Polygon_Main_PriorityFee());

        if (provider && chainID && address) {
            const tx = await userApproveACreed_to_Dice(
                makeToast,
                Number(ProposeGasPrice).toFixed(0),
                NP.plus( Number(ProposeGasPrice), Number(suggestBaseFee) ).toFixed(0),
            )
            tx ? setACreedApproveEnough(true) : null;
        }
    }

    useEffect(() => {

        const asyncGas = async () => {
            return await get_Polygon_Main_PriorityFee()
        }
        asyncGas().then(
            e => {
                setPolygon_main_priorityFee(e)
            }
        ).catch(console.error)
    }, [])

    return (
        <div className="gloableIDO">
            <IdoTopBar />

            <Toaster
                position="top-left"
                toastOptions={{ style: { background: "#363636", color: "#fff" } }}
                containerStyle={{ position: "sticky", top: "35px" }}
            />

            <Box className='containIDO' >
                <Box >
                    <div className="taglib">{t("CreedOne")}</div>
                    <Grid
                        container
                        spacing={0}
                        display="flex"
                        flexDirection={`${isSmallPhone ? "column" : "row"}`}
                        alignItems="center"
                    >
                        <Grid item xs={6} component="div" className='AcItem'
                            display="flex" flexDirection="column" justifyContent="center"
                            alignSelf={`${isSmallPhone ? "center" : "flex-start"}`}
                            sx={{ padding: "0 3rem" }}
                        >
                            <div>{t("FundraisingGoals")}</div>
                            <div>{<CountUp start={0} end={toTalAmount} decimals={0} suffix=" Matic" />}</div>
                            <div className="divideR" />
                            <Box display="flex" flexDirection="column" sx={{ width: "100%" }} className="infoList">
                                <Box
                                    className="infoItem"
                                    display="flex"
                                    justifyContent="space-between"
                                    align="middle"
                                >
                                    <div>{t("hasbeenraised")}</div>
                                    <div>{<CountUp start={0} preserveValue={true} end={sellAmount} decimals={1} suffix=" Matic" />}</div>
                                </Box>
                                <Box
                                    className="infoItem"
                                    display="flex"
                                    justifyContent="space-between"
                                    align="middle"
                                >
                                    <div>{t("price_per_token")}</div>
                                    <div>{<CountUp start={0} end={salePrice} decimals={1} suffix=" Matic" />}</div>
                                </Box>
                                <Box
                                    className="infoItem"
                                    display="flex"
                                    justifyContent="space-between"
                                    align="middle"
                                >
                                    <div>{t("Whitelisted_or_not")}</div>
                                    <div className="flex flex-align-items-center ">
                                        <Avatar
                                            className="no flex flex-align-items-center"
                                            src={whiteListed ? yes : no}
                                            sx={{ width: 24, height: 24 }}
                                        />
                                        <div className="ml-8"> {whiteListed ? t("Yes_Discount") : t("No_Discount")}</div>
                                    </div>
                                </Box>
                                <Box
                                    className="infoItem"
                                    display="flex"
                                    justifyContent="space-between"
                                    align="middle"
                                >
                                    <div>{t("Participated_or_not")}</div>
                                    <div className="flex flex-align-items-center ">
                                        <Avatar
                                            className="yes flex flex-align-items-center"
                                            src={boughtACreed ? yes : no}
                                            sx={{ width: 24, height: 24 }}
                                        />
                                        <div className="ml-8">{boughtACreed ? t("Yes") : t("No")}</div>
                                    </div>
                                </Box>
                            </Box>

                            <Box display="flex" flexDirection="column"
                                alignItems="center" className="box-l-f">
                                <Box className="SALE-STARTS-IN" justifyContent="center">
                                    SALE {t(startText)} IN
                                </Box>
                                <Box display="flex" justifyContent="space-around" sx={{ width: "100%" }} className="time">
                                    <Box className="timeItem " display="flex" flexDirection="column" alignItems="center" >
                                        <div className="countDown" >
                                            {coundown.day}
                                        </div>
                                        <div className="downDesc ">Day</div>
                                    </Box>
                                    <div className="line">:</div>
                                    <Box className="timeItem " display="flex" flexDirection="column" alignItems="center" >
                                        <div className="countDown">
                                            {coundown.hour}
                                        </div>
                                        <div className="downDesc ">Hours</div>
                                    </Box>
                                    <div className="line">:</div>
                                    <Box className="timeItem " display="flex" flexDirection="column" alignItems="center" >
                                        <div className="countDown" >
                                            {coundown.minutes}
                                        </div>
                                        <div className="downDesc ">Minutes</div>
                                    </Box>
                                    <div className="line">:</div>
                                    <Box className="timeItem " display="flex" flexDirection="column" alignItems="center" >
                                        <div className="countDown" >
                                            {coundown.seconds}
                                        </div>
                                        <div className="downDesc ">Seconds</div>
                                    </Box>
                                </Box>
                            </Box>
                        </Grid>

                        <Grid item xs={6} component="div" className='AcItem AcItem-Bottom'
                            display="flex" flexDirection="column" justifyContent="center"
                            alignSelf={`${isSmallPhone ? "center" : "flex-start"}`}
                            sx={{ padding: "0 3rem" }}
                        >
                            <Box display="flex" flexDirection="column" justifyContent="center" alignItems="center">
                                <div>{t("CanGetFromIDO")}</div>
                                <Box display="flex" flexDirection="row" justifyContent="center" alignItems="center">
                                    <Avatar src={IDOLogo} sx={{ width: 30, height: 30 }} />
                                    {<CountUp start={0} preserveValue={true} end={willGetACreed} decimals={!isTimeEnd ? 2 : 4} prefix="&nbsp;" suffix=" Assassin's Creed" />}
                                </Box>
                            </Box>
                            <div>{t("advanceDeposit")}</div>
                            <Box display="flex" flexDirection="column" sx={{ paddingBottom: '1.5rem' }}>
                                <Box display="flex" alignSelf="flex-end" alignItems="center">
                                    <CountUp start={0} preserveValue={true} end={userMaticBalance} decimals={2} prefix={t("yourBalance")} suffix=" Matic" />
                                </Box>
                                <Box display="flex" justifyContent="space-between" alignItems="center">
                                    <FormControl variant="outlined" sx={{ color: "#FFFFFF" }} fullWidth >
                                        <InputLabel htmlFor="outlined-inputMatic" className='baseFontFamily'>Matic</InputLabel>
                                        <OutlinedInput
                                            id="outlined-inputMatic"
                                            className='baseFontFamily'
                                            value={maticAmount}
                                            onChange={onChangeHandle_IDO}
                                            inputProps={{
                                                step: 0.01,
                                                min: Number(minAmount),
                                                max: Number(maxAmount),
                                                type: "number",
                                            }}
                                            labelWidth={55}
                                            endAdornment={
                                                <Button hidden={true} position="end"
                                                    className="inputMax" sx={{ color: '#a659ff', fontWeight: "600" }}
                                                    onClick={handleInputMax_IDO}
                                                    endIcon={<Avatar src={maticLogo} />}
                                                >
                                                    Max
                                                </Button>
                                            }
                                        />
                                    </FormControl>
                                    <Box>

                                    </Box>
                                </Box>
                            </Box>
                            {address && connected ?
                                <Button
                                    variant="outlined"
                                    className="pleceBetBtn IDOBtn hvr-float-shadow"
                                    onClick={IDOHandle}
                                    disabled={isPendingTxn(pendingTransactions, "purchaseaACreed")}
                                >
                                    <Typography variant="h6" className="Arial-Black">
                                        {txnButtonText(pendingTransactions, "purchaseaACreed", "IDO")}
                                    </Typography>
                                </Button>
                                :
                                <Link href={returnMetaMaskLink()} target="_blank" className="MetaMaskLink">
                                    <Box display="flex" justifyContent="center" alignItems="center">
                                        <img src={metamaskgif} className="MetaMaskGif" />
                                        <Typography variant="h6" className="MetaMaskDesc">{t('DownloadMetaMask')}</Typography>
                                    </Box>
                                </Link>
                            }

                        </Grid>

                    </Grid>
                </Box>

                <Box>
                    <div className="taglib">{t("CreedTwo")}</div>
                    <Grid
                        container
                        // xs={12}
                        spacing={0}
                        display="flex"
                        flexDirection={`${isSmallPhone ? "column" : "row"}`}
                        alignItems="center"
                    >

                        <Grid item xs={6} component="div" className='AcItem'
                            display="flex" flexDirection="column" justifyContent="center"
                            alignSelf={`${isSmallPhone ? "center" : "flex-start"}`}
                            sx={{ padding: "0 3rem" }}
                        >
                            <Box display="flex" justifyContent="space-between">
                                <CountUp start={0} preserveValue={true} end={companyTotalAssets} decimals={2}
                                    prefix={t("Total_company_assets")} suffix=" Matic" />
                            </Box>
                            <Box display="flex" justifyContent="space-between">
                                <div>
                                    <Link href='https://Dice4Utopia.com' target="_blank" style={{ color: 'aquamarine' }}>Dice4Utopia</Link>
                                    {t("shareholderIncome")}
                                </div>
                                <div>
                                    {
                                        Number(FullRelease) > 0 ?
                                            <CountUp start={0} preserveValue={true} end={FullRelease} decimals={2}
                                                prefix={"☮ "} suffix=" Matic" />
                                            :
                                            Number(ProfitRelease) > 0 ?
                                                <CountUp start={0} preserveValue={true} end={ProfitRelease} decimals={2}
                                                    prefix={t("profit")} suffix=" Matic" />
                                                :
                                                <CountUp start={0} preserveValue={true} end={NP.minus(Number(outcomeProfit), Number(incomeProfit)).toFixed(2)} decimals={2}
                                                    prefix={t("loss")} suffix=" Matic" />
                                    }
                                </div>
                            </Box>
                            <Box display="flex" justifyContent="space-between">
                                <div>{t("ACreed_you_hold")}</div>
                                <CountUp start={0} preserveValue={true} end={userACreedBalance} decimals={1} suffix=" ACreed" />
                            </Box>
                            <Box display="flex" justifyContent="space-between">
                                <div>{t("Your_shareholding_in_Dice4Utopia")}</div>
                                <CountUp start={0} preserveValue={true} end={diceSharesForShow} decimals={2} suffix=" %" />
                            </Box>
                            <Box display="flex" justifyContent="space-between" sx={{ paddingBottom: "1rem" }}>
                                <div>{t("Your_in_other_products")}</div>
                                <div>{t("comingSoon")}</div>
                            </Box>
                            <Typography sx={{ paddingBottom: "0.5rem" }}>{t("Destruction_Shares")}</Typography>

                            <FormControl variant="outlined" color="secondary" fullWidth
                            >
                                <InputLabel htmlFor="outlined-inputACreed"
                                    className='baseFontFamilyACreed'>{t("Product_Shares")}</InputLabel>
                                <OutlinedInput
                                    id="outlined-inputACreed"
                                    className='baseFontFamilyACreed'
                                    value={burnSharesAmount}
                                    onChange={onChangeHandle_burnShares}
                                    inputProps={{
                                        step: 0.01,
                                        min: 0,
                                        type: "number",
                                    }}
                                    labelWidth={120}
                                    endAdornment={
                                        <Button position="end"
                                            className="inputMax" sx={{ color: "#2c2628", fontWeight: "600" }}
                                            onClick={handleInputMax_burnShares}
                                            endIcon={<Avatar src={IDOLogo} />}
                                        >
                                            Max
                                        </Button>
                                    }
                                />
                            </FormControl>
                            <div>
                                <CountUp start={0} preserveValue={true} end={burnSharesAmount} decimals={2}
                                    prefix={t("now_you_will_destroy")} suffix=" Assassin's Creed ☮" />
                            </div>
                            <div>
                                <CountUp start={0} preserveValue={true} end={willGetMatic} decimals={2}
                                    prefix={t("Get_it_from_Dice4Utopia")} suffix=" Matic" />
                            </div>
                            {address && connected ?
                                <Button
                                    variant="outlined"
                                    className="pleceBetBtn BurnACreedBtn hvr-float-shadow"
                                    onClick={aCreedApproveEnough ? DiceWithdrawalIncomeHandle : userApproveACreed_to_DiceHandle}
                                    disabled={isPendingTxn(pendingTransactions, "WithdrawalIncome") || isPendingTxn(pendingTransactions, "userApproveACreed_to_Dice")}
                                >
                                    <Typography variant="h6" className="Arial-Black">
                                        {aCreedApproveEnough ?
                                            txnButtonText(pendingTransactions, "WithdrawalIncome", 'Withdrawal Income')
                                            :
                                            txnButtonText(pendingTransactions, "userApproveACreed_to_Dice", 'Approve ACreed')
                                        }
                                    </Typography>
                                </Button>
                                :
                                <Link href={returnMetaMaskLink()} target="_blank" className="MetaMaskLink">
                                    <Box display="flex" justifyContent="center" alignItems="center">
                                        <img src={metamaskgif} className="MetaMaskGif" />
                                        <Typography variant="h6" className="MetaMaskDesc">{t('DownloadMetaMask')}</Typography>
                                    </Box>
                                </Link>
                            }

                        </Grid>

                        <Grid item xs={6} component="div" className='AcItem AcItem-Bottom'
                            display="flex" flexDirection="column" justifyContent="center"
                            alignSelf={`${isSmallPhone ? "center" : "flex-start"}`}
                            sx={{ padding: "1rem 3rem" }}>
                            <Box display='flex' alignItems='center' sx={{ width: '100%', height: "2rem" }} >
                                <CountUp start={0} preserveValue={true} end={SatanDevil} decimals={0}
                                    prefix="🔥ACreed X" suffix="% 👹" />
                                <Box sx={{ flex: 1, mr: 1 }}>
                                    <LinearProgress variant={`${SatanDevil ? "determinate" : "indeterminate"}`} value={NP.divide(SatanDevil, 4)} />
                                </Box>
                                <CountUp start={0} end={(SatanDevil / 4).toFixed(0)} decimals={0} suffix="%" />
                            </Box>
                            <Box className="SALE-STARTS-IN" justifyContent="center">
                                <Typography>
                                    {Number(FullRelease) > 0 ? 'Release All Assets Had Started ☮ Freedom !'
                                        : `Release All Assets Of Dice4Utopia ${startTextRelease} IN`
                                    }
                                </Typography>
                            </Box>
                            <Box display="flex" justifyContent="space-around" sx={{ width: "100%" }} className="time">
                                <Box className="timeItem " display="flex" flexDirection="column" alignItems="center" >
                                    <div className="countDown" >
                                        {coundownRelease.day}
                                    </div>
                                    <div className="downDesc ">Day</div>
                                </Box>
                                <div className="line">:</div>
                                <Box className="timeItem " display="flex" flexDirection="column" alignItems="center" >
                                    <div className="countDown">
                                        {coundownRelease.hour}
                                    </div>
                                    <div className="downDesc ">Hours</div>
                                </Box>
                                <div className="line">:</div>
                                <Box className="timeItem " display="flex" flexDirection="column" alignItems="center" >
                                    <div className="countDown" >
                                        {coundownRelease.minutes}
                                    </div>
                                    <div className="downDesc ">Minutes</div>
                                </Box>
                                <div className="line">:</div>
                                <Box className="timeItem " display="flex" flexDirection="column" alignItems="center" >
                                    <div className="countDown" >
                                        {coundownRelease.seconds}
                                    </div>
                                    <div className="downDesc ">Seconds</div>
                                </Box>
                            </Box>
                            <Box display="flex" alignSelf="flex-start" sx={{ paddingTop: '2rem' }}>
                                <Typography>{t("finallyReleased")}</Typography>
                            </Box>
                            <Box display="flex" justifyContent="space-between" alignSelf="center" sx={{ paddingTop: '1rem' }}>
                                <CountUp start={0} preserveValue={true} end={companyTotalAssetsFull} decimals={2}
                                    prefix={t("Total_company_assets")} suffix=" Matic" />
                            </Box>
                            <Box display="flex" justifyContent="space-between" alignSelf="center" sx={{ paddingTop: '0.5rem' }}>
                                <div>
                                    <Link href='https://Dice4Utopia.com' target="_blank" style={{ color: 'aquamarine' }}>Dice4Utopia</Link>
                                </div>
                                <div>
                                    <CountUp start={0} preserveValue={true} end={Exodus} decimals={2}
                                        prefix={t("shareholderIncome")} suffix=" Matic" />

                                </div>
                            </Box>
                            <Box display="flex" flexDirection="column" alignSelf="flex-start" sx={{ paddingTop: '1.5rem' }}>
                                <div>
                                    <CountUp start={0} preserveValue={true} end={burnSharesAmount} decimals={2}
                                        prefix={t("After_Release")} suffix=" Assassin's Creed ☮" />
                                </div>
                                <div>
                                    <CountUp start={0} preserveValue={true} end={willGetMaticFull} decimals={2}
                                        prefix={t("Get_it_from_Dice4Utopia")} suffix=" Matic" />
                                </div>
                            </Box>
                        </Grid>
                    </Grid>
                </Box>
            </Box>

            {isSmallPhone &&
                <Box display="flex" justifyContent="center" alignItems="center" sx={{ minHeight: "6rem" }}>
                    <Link href="https://metamask.app.link/dapp/dao2utopia.com" color="#7fb5e9" underline="hover">{t("Open_in_MetaMaskAPP")}</Link>
                </Box>
            }
        </div>
    );
}
