import { useWeb3Context } from '../../../hooks/web3Context'
import { shorten } from '../../../utils';
import metaMaskIcon from '../../../assets/images/wallet/metamask.png';

import { ReactComponent as Peace_sign } from "../../../assets/images/Peace_sign.svg";
import browserIcon from '../../../assets/images/wallet/browser.png';
import logOutIcon from '../../../assets/images/wallet/logOut.png';
import './userInfo.scss'
import { Avatar, Box, Button, Dialog, Link } from '@mui/material';
import { Trans, useTranslation } from "react-i18next";
import { useSelector } from 'react-redux';
import CountUp from "react-countup";

export default function WalletModal({ isShowWallet, setShowWallet }) {
	const { address, chainID, disconnect } = useWeb3Context()
	const { t } = useTranslation();

	const sharesDice = useSelector(state => { return state.account?.accountDetails.sharesDice || '0'; });



	return (
		<Dialog
			open={isShowWallet}
			onClose={() => setShowWallet(false)}
			footer={null} id='walletInfo-wrap'>

			<div className="walletInfo color5">
				<Box className="title" display="flex" justifyContent='space-between'>
					<div className="font-weight-b font-family-Kano" style={{ color: "#000000", fontSize: "18px" }}>
						{t('connectedwallet')}
					</div>
				</Box>
				<Box className="box" display="flex" alignItems="center">
					<div className="walletIcon" >
						<Avatar variant="circular" component={Peace_sign} />
					</div>
					<div className='box-r'>
						<div className="walletAddress font-12 color4 font-family-Kano">
							{shorten(address)}{" "}{t('Dice4Utopiasharesheld')}
							<CountUp start={0} end={sharesDice} decimals={2} prefix={" "} suffix=" %" />
						</div>
						<Box className="operations mt-12 font-16" display="flex" justifyContent='space-between'>
							<Link className="operationItem flex flex-align-items-center viewInExplorer"
								underline="none"
								href={`https://polygonscan.com/address/${address}`} target="_blank">
								<div className="color4  font-family-Kano">{t('Creed')}</div>
							</Link>
						</Box>
					</div>
				</Box>
				<Box className='footer font-12 mt-10' display="flex" justifyContent='space-between' alignItems='center'>
					<Box onClick={disconnect} className="logOutBtn" display="flex" alignItems='center'>
						<Avatar className="logOutIcon flex flex-align-items-center" variant="rounded" src={logOutIcon} />
						<div className="color4 font-12 font-family-Kano" >LOG OUT</div>
					</Box>
					<Box>
						<Button className="close " onClick={() => setShowWallet(false)} > Close </Button>
					</Box>
				</Box>
			</div>
		</Dialog >
	)
}