import { useState } from "react";
import { NavLink } from "react-router-dom";
import { useMediaQuery } from "react-responsive";
import "./index.scss";
import { ReactComponent as Peace_sign } from "../../assets/images/Peace_sign.svg";
import { ReactComponent as metaMaskSvg } from '../../assets/images/wallet/metaMaskSvg.svg';

import {
  Avatar,
  Box,
  Button,
  Typography,
  Link,
} from "@mui/material";
import downIcon from '../../assets/images/down.svg';
import { useWeb3Context } from "../../hooks/web3Context";
import { shorten } from "../../utils";
import WalletModal from "../TopBar/userInfo";
import Language from "../Language";


export default function IdoTopBar() {
  const isSmall = useMediaQuery({ maxWidth: 800 });
  const isPhone = useMediaQuery({ maxWidth: 550 });

  const { address, chainID, connect, connected } = useWeb3Context();

  const [isShowWallet, setShowWallet] = useState(false);

  const safeConnectMetaMask = async () => {
    try {
      await ethereum.request({
        method: 'wallet_switchEthereumChain',
        params: [{ chainId: '0x89' }],
      });
    } catch (switchError) {
      // This error code indicates that the chain has not been added to MetaMask.
      if (switchError.code === 4902) {
        try {
          await ethereum.request({
            method: 'wallet_addEthereumChain',
            params: [
              {
                chainId: '0x89',
                chainName: 'Polygon MainNet',
                nativeCurrency: {
                  name: 'Matic',
                  symbol: 'Matic', // 2-6 characters long
                  decimals: 18,
                },
                rpcUrls: ['https://polygon-rpc.com/'],
                blockExplorerUrls: ['https://polygonscan.com/'],
                iconUrls: ['https://github.com/maticnetwork/polygon-token-assets/blob/main/icons/matic.svg']
              },
            ],
          });
        } catch (addError) {
          // handle "add" error
          console.warn(addError);
        }
      }
      // handle other "switch" errors
      console.warn(switchError);

    } finally {
      connect()
    }

  }


  return (
    <Box className="head-ido" display="flex" justifyContent="space-between">

      {isPhone ? (
        <Box
          display="flex"
          alignItems="center"
          flexDirection="column"
        >
          <Avatar
            component={Peace_sign}
            className="hvr-grow-rotate"
            sx={{ height: "3rem", width: "3rem" }}
          />
          <Link underline="none" color="#FCFCFC" component={NavLink} to="/" className="flex-align-self-start">
            <Box display="flex">
              <Typography className="font-family-RACE1-Brannt-Plus-NCV logoDesc-ido">
                Dao
              </Typography>
              <Typography className="rotatedIDO">2</Typography>
              <Typography className="font-family-RACE1-Brannt-Plus-NCV logoDesc-ido">
                Utopia
              </Typography>
            </Box>
          </Link>
        </Box>
      ) : (
        <Box
          display="flex"
          alignItems="center"
          justifyContent="space-between"
          sx={{ width: `${isSmall ? "15rem" : "14rem"}` }}
        >
          <Avatar
            component={Peace_sign}
            className="hvr-grow-rotate"
            sx={{ height: `${isSmall ? "5rem" : "3.5rem"}`, width: `${isSmall ? "5rem" : "3.5rem"}` }}
          />

          <Link underline="none" color="#FCFCFC" component={NavLink} to="/" className="flex-align-self-start">
            <Box display="flex" >
              <Typography className="font-family-RACE1-Brannt-Plus-NCV logoDesc-ido" >
                Dao
              </Typography>
              <Typography className="rotatedIDO">2</Typography>
              <Typography className="font-family-RACE1-Brannt-Plus-NCV logoDesc-ido">
                Utopia
              </Typography>
            </Box>
          </Link>
        </Box>
      )}

      <Box
        display="flex"
        flexDirection={`${isSmall ? "column-reverse" : "row"}`}
        justifyContent="space-between"
        alignItems="center"
      >
        <Language />

        {address && address.length > 0 ? (
          <Button variant="contained"
            className="address-ido"
            onClick={() => setShowWallet(true)}
          >
            <div>{shorten(address)}</div>
            <Avatar className="downIcon" src={downIcon} sx={{ width: "20px", height: "20px" }} />
          </Button>
        ) : null}
        {!connected || !address?
          <Button className="address-ido" onClick={safeConnectMetaMask} sx={{ color: "inherit", fontFamily: "inherit" }}
            startIcon={<Avatar component={metaMaskSvg} variant="rounded" className="address-ido-startIcon" />}>
            ConnectWallet
          </Button>
          : null}
        <WalletModal isShowWallet={isShowWallet} setShowWallet={setShowWallet} />
      </Box>

    </Box>
  );
}
