import { useEffect, useState } from "react";
import { useMediaQuery } from "react-responsive";
import { useTranslation } from "react-i18next";

import { ReactComponent as zh_Icon } from "../../assets/images/Language/zh_CN.svg";
import { ReactComponent as en_Icon } from "../../assets/images/Language/en_US.svg";
import { ReactComponent as fr_Icon } from "../../assets/images/Language/fr_FR.svg";
import { ReactComponent as ru_Icon } from "../../assets/images/Language/ru_RU.svg";
import {
  Avatar,
  SvgIcon,
  Box,
  Button,
  Typography,
  Link,
  Popper,
  Fade,
  Paper,
} from "@mui/material";
import "./index.scss"

export default function Language() {
  const isSmall = useMediaQuery({ maxWidth: 920 });
  const isPhone = useMediaQuery({ maxWidth: 550 });

  const lngs = {
    fr: { nativeName: "Français", icon: fr_Icon },
    ru: { nativeName: "русский", icon: ru_Icon },
    en: { nativeName: "English", icon: en_Icon },
    zh: { nativeName: "简体中文", icon: zh_Icon },
  };
  const { i18n } = useTranslation();
  const [anchorEl, setAnchorEl] = useState(null);

  const handleClick = (event) => {
    setAnchorEl(anchorEl ? null : event.currentTarget);
  };
  const changeLanguageHandle = (lng) => {
    handleClick({ currentTarget: "currentTarget" })
    i18n.changeLanguage(lng);
    localStorage.setItem("i18nextLng", lng);
  };

  const open = Boolean(anchorEl);
  const id = "language-popper";

  const Phone_handleClick = (e) => { 
    isPhone ? handleClick(e) : null;
  }
  return (
    <Box component="div" onMouseEnter={e => handleClick(e)} onMouseLeave={e => handleClick(e)} className="language">
      <Button variant="contained" onClick={e => Phone_handleClick(e)} className="selectLanguageBtn language-btn unSVG">
        <Avatar
          variant="rounded"
          component={lngs[i18n.language].icon}
          className="unSVG"
          style={{ width: "24px", height: "24px" }}
        />
        <Typography style={{ marginLeft: "5px" }}>
          {lngs[i18n.language].nativeName}
        </Typography>
      </Button>

      <Popper
        id={id}
        open={open}
        anchorEl={anchorEl}
        placement="bottom-start"
        transition 
      >
        {({ TransitionProps }) => {
          return (
            <Fade {...TransitionProps} timeout={100}>
              <Paper className="language-menu" elevation={0} >
              <Box display="flex" flexDirection="column" justifyContent="center" alignItems="center" >
                  {Object.keys(lngs)
                    .filter((lng) => lng != i18n.language)
                    .map((lng) => (
                      <Button fullWidth 
                        variant="contained"
                        key={lng}
                        className="hvr-bounce-to-right lang-btn"
                        onClick={() => changeLanguageHandle(lng)}
                        endIcon={
                          <Avatar
                            variant="rounded"
                            component={lngs[lng].icon}
                            className="unSVG"
                            style={{ width: "24px", height:"24px" }}
                          />
                        }
                      >
                        <Typography noWrap>{lngs[lng].nativeName}</Typography>
                      </Button>
                    ))}
                </Box>
              </Paper>
            </Fade>
          );
        }}
      </Popper>
    </Box>

  );
}
