import React, { useState, useEffect, useCallback } from 'react'
import { useWeb3Context } from './web3Context'
import { errors, ethers } from 'ethers'
import { abi as IDOABI } from '../config/abi/ido.json'
import { abi as ierc20 } from '../config/abi/ierc20.json'
import { abi as diceABI } from "../config/abi/dice.json"
import { addresses } from "../constants"
import { clearPendingTxn, fetchPendingTxns } from '../slices/PendingTxnsSlice'
import { useDispatch } from 'react-redux'
import { addTokenToWallet, numberStrToBigNumber, stringTrim } from '../utils'
import { IJsonRPCError } from '../slices/interfaces'
import { loadAppDetails } from '../slices/AppSlice'
import { loadAccountDetails } from '../slices/AccountSlice'
import logo from '../assets/images/ACreed.png'




export default function useIdo() {

    const { address, provider, chainID } = useWeb3Context()
    const dispatch = useDispatch()



    const userApproveACreed_to_Dice = useCallback(
        async (makeToast: Function, MaxPriorityFeePerGas: string, MaxFeePerGas: string) => {
            if (provider && chainID && address) {
                const signer = provider.getSigner()
                const aCreedContract = new ethers.Contract(addresses[chainID].ACreed_ADDRESS, ierc20, signer)
                let tx
                try {
                    tx = await aCreedContract.approve(
                        addresses[chainID].Dice_ADDRESS,
                        ethers.utils.parseUnits('88888888', 'ether'),
                        {
                            maxPriorityFeePerGas: numberStrToBigNumber(MaxPriorityFeePerGas, 9),
                            maxFeePerGas: numberStrToBigNumber(MaxFeePerGas, 9),
                        }
                    )
                    dispatch(
                        fetchPendingTxns({ txnHash: tx.hash, text: 'userApproveACreed_to_Dice', type: 'userApproveACreed_to_Dice' })
                    )

                    await tx.wait()

                } catch (e) {
                    console.warn({e});

                    const rpcError = e as IJsonRPCError;
                    if (rpcError.reason) { 
                        makeToast(rpcError.reason)
                    } else { 

                        if (rpcError.data) {
                            makeToast(rpcError.data.message)
                        } else {
                            if (rpcError.error && rpcError.error.data) {
                                makeToast(rpcError.error.data.message)
                            } else {
                                rpcError.error ? makeToast(rpcError.error.message) : makeToast(rpcError.message);
                            }
                        }
                    }

                } finally {
                    if (tx) {
                        addTokenToWallet(addresses[chainID].ACreed_ADDRESS, 'ACreed', 18, logo)

                        dispatch(
                            clearPendingTxn(tx.hash)
                        )
                        dispatch(loadAppDetails({ networkID: chainID, provider: provider }));
                        dispatch(loadAccountDetails({ networkID: chainID, provider: provider, address: address }));
                    }
                }
            }
        }, [provider, chainID, address])

    const WithdrawalIncome = useCallback(
        async (amount: string, makeToast: Function, MaxPriorityFeePerGas: string, MaxFeePerGas: string) => {
            if (provider && chainID && address) {
                const signer = provider.getSigner()
                const diceContract = new ethers.Contract(addresses[chainID].Dice_ADDRESS, diceABI, signer)
                let tx
                try {
                    tx = await diceContract.WithdrawalIncome(
                        ethers.utils.parseUnits(amount, 'ether'),
                        { 
                            maxPriorityFeePerGas: numberStrToBigNumber(MaxPriorityFeePerGas, 9),
                            maxFeePerGas: numberStrToBigNumber(MaxFeePerGas, 9),
                        }
                        )
                    dispatch(
                        fetchPendingTxns({ txnHash: tx.hash, text: 'WithdrawalIncome', type: 'WithdrawalIncome' })
                    )

                    await tx.wait()

                } catch (e) {
                    console.warn({e});

                    const rpcError = e as IJsonRPCError;
                    if (rpcError.reason) { 
                        makeToast(rpcError.reason)
                    } else { 
                        
                        if (rpcError.data) {
                            makeToast(rpcError.data.message)
                        } else {
                            if (rpcError.error && rpcError.error.data) {
                                makeToast(rpcError.error.data.message)
                            } else {
                                rpcError.error ? makeToast(rpcError.error.message) : makeToast(rpcError.message);
                            }
                        }
                    }

                } finally {
                    if (tx) {

                        dispatch(
                            clearPendingTxn(tx.hash)
                        )
                        dispatch(loadAppDetails({ networkID: chainID, provider: provider }));
                        dispatch(loadAccountDetails({ networkID: chainID, provider: provider, address: address }));
                    }
                }
            }
        }
        , [provider, chainID, address])


    const IDoClaim = useCallback(
        async (amount: string, makeToast: Function, MaxPriorityFeePerGas: string, MaxFeePerGas: string) => {
            if (provider && chainID && address) {
                const signer = provider.getSigner()
                const idoContract = new ethers.Contract(addresses[chainID].Ido_ADDRESS, IDOABI, signer)
                let tx
                try {
                    tx = await idoContract.purchaseaACreed(ethers.utils.parseUnits(amount, "ether"), address,
                        {
                            value: ethers.utils.parseUnits(amount, "ether"),
                            maxPriorityFeePerGas: numberStrToBigNumber(MaxPriorityFeePerGas, 9),
                            maxFeePerGas: numberStrToBigNumber(MaxFeePerGas, 9),
                        }
                    )
                    dispatch(
                        fetchPendingTxns({ txnHash: tx.hash, text: 'purchaseaACreed', type: 'purchaseaACreed' })
                    )
                    await tx.wait()
                } catch (e) {
                    console.warn({e});

                    const rpcError = e as IJsonRPCError;
                    if (rpcError.reason) { 
                        makeToast(rpcError.reason)
                    } else { 
                        
                        if (rpcError.data) {
                            makeToast(rpcError.data.message)
                        } else {
                            if (rpcError.error && rpcError.error.data) {
                                makeToast(rpcError.error.data.message)
                            } else {
                                rpcError.error ? makeToast(rpcError.error.message) : makeToast(rpcError.message);
                            }
                        }
                    }

                } finally {
                    if (tx) {
                        addTokenToWallet(addresses[chainID].ACreed_ADDRESS, 'ACreed', 18, logo)

                        dispatch(
                            clearPendingTxn(tx.hash)
                        )
                        dispatch(loadAppDetails({ networkID: chainID, provider: provider }));
                        dispatch(loadAccountDetails({ networkID: chainID, provider: provider, address: address }));
                    }
                }
            }
        }, [provider, chainID, address])


    const Sermon = useCallback(
        async (amount: string, makeToast: Function, MaxPriorityFeePerGas: string, MaxFeePerGas: string) => {
            if (provider && chainID && address) {
                const signer = provider.getSigner()
                const idoContract = new ethers.Contract(addresses[chainID].Ido_ADDRESS, IDOABI, signer)
                let tx
                try {
                    tx = await idoContract.sermon( ethers.utils.parseUnits(amount, "ether"),
                        {
                            value: ethers.utils.parseUnits(amount, "ether"),
                            maxPriorityFeePerGas: numberStrToBigNumber(MaxPriorityFeePerGas, 9),
                            maxFeePerGas: numberStrToBigNumber(MaxFeePerGas, 9),
                        }
                    )
                    dispatch(
                        fetchPendingTxns({ txnHash: tx.hash, text: 'purchaseaACreed', type: 'purchaseaACreed' })
                    )
                    await tx.wait()
                } catch (e) {
                    console.warn({e});

                    const rpcError = e as IJsonRPCError;
                    if (rpcError.reason) { 
                        makeToast(rpcError.reason)
                    } else { 
                        
                        if (rpcError.data) {
                            makeToast(rpcError.data.message)
                        } else {
                            if (rpcError.error && rpcError.error.data) {
                                makeToast(rpcError.error.data.message)
                            } else {
                                rpcError.error ? makeToast(rpcError.error.message) : makeToast(rpcError.message);
                            }
                        }
                    }
                    // if (rpcError.data) {
                    //     // dispatch(error(rpcError.data.message))
                    //     makeToast(rpcError.data.message)
                    // } else {
                    //     if (rpcError.error) {
                    //         makeToast(rpcError.error.message)
                    //     } else {
                    //         // dispatch(error(rpcError.message))
                    //         makeToast(rpcError.message)
                    //     }
                    // }

                } finally {
                    if (tx) {
                        addTokenToWallet(addresses[chainID].ACreed_ADDRESS, 'ACreed', 18, logo)

                        dispatch(
                            clearPendingTxn(tx.hash)
                        )
                        dispatch(loadAppDetails({ networkID: chainID, provider: provider }));
                        dispatch(loadAccountDetails({ networkID: chainID, provider: provider, address: address }));
                    }
                }
            }
        }, [provider, chainID, address])



    return {
        IDoClaim, Sermon, userApproveACreed_to_Dice, WithdrawalIncome
    }
}